import React from "react";
import {
  lifecycle,
  withStateHandlers,
  branch,
  renderComponent,
  compose,
} from "recompose";

import { SuccessText } from "./elements.jsx";

const defaultSuccessMessage =
  "Thank you for your request. A representative will reach out soon.";

const baseUrl = "//app-ab35.marketo.com";
const formsScriptSrc = `${baseUrl}/js/forms2/js/forms2.min.js`;

const _UnstyledMarketoForm = ({ formId }) => <form id={`mktoForm_${formId}`} />;

const _lifecycle = {
  componentDidMount() {
    const {
      munchkinId,
      formId,
      setFormLoaded,
      setSuccess,
      hideErrorsUntilSubmit,
    } = this.props;

    const formsScript = document.createElement("script");
    formsScript.src = formsScriptSrc;
    formsScript.onload = () => {
      // this script should load MktoForms2

      // need to use custom loadForm because the built-in success handler
      // fires for all forms on a page

      const loadForm = window.MktoForms2.loadForm.bind(
        window.MktoForms2,
        baseUrl,
        munchkinId,
        formId,
      );

      // Add styles to hide errors by default if hideErrorsUntilSubmit
      const hideMarketoErrorsStyle = document.createElement("style");
      hideMarketoErrorsStyle.type = "text/css";
      hideMarketoErrorsStyle.innerHTML =
        ".mktoError { display: none !important; }";
      if (hideErrorsUntilSubmit) {
        document
          .getElementsByTagName("head")[0]
          .appendChild(hideMarketoErrorsStyle);
      }

      window.MktoForms2.whenReady((form) => {
        // Marketo forms load with a bunch of terrible inline styles and CSS

        // Remove <style> tags from <head>
        if (document.getElementById("mktoForms2BaseStyle")) {
          document.getElementById("mktoForms2BaseStyle").remove();
        }

        if (document.getElementById("mktoForms2ThemeStyle")) {
          document.getElementById("mktoForms2ThemeStyle").remove();
        }

        // Remove inline styles from <form> element and all descendants
        const formEl = form.getFormElem()[0];
        const styledEls = Array.from(formEl.querySelectorAll("[style]")).concat(
          formEl,
        );
        styledEls.forEach((el) => el.removeAttribute("style"));

        // Remove <style> tag that's appended to the <form>
        const appendedStyle = formEl.querySelector("style");
        if (appendedStyle) {
          formEl.removeChild(appendedStyle);
        }

        window.setTimeout(setFormLoaded, 150);
      });

      loadForm((form) => {
        // cannot use the built-in success handler because it
        // fires for all forms on a page

        form.onSuccess(() => {
          // call state handler
          setSuccess();

          // prevent default behavior (navigating away from page)
          return false;
        });

        form.onValidate(() => {
          // onValidate seems to be only triggered on a form submit attempt
          // Add more styles to override previous styles
          // I'm so sorry to whoever has to clean up this mess
          if (hideErrorsUntilSubmit) {
            const showMarketoErrorsStyle = document.createElement("style");
            showMarketoErrorsStyle.type = "text/css";
            showMarketoErrorsStyle.innerHTML =
              ".mktoError { display: block !important; }";
            if (hideErrorsUntilSubmit) {
              document
                .getElementsByTagName("head")[0]
                .appendChild(showMarketoErrorsStyle);
            }
          }
        });
      });
    };

    document.body.append(formsScript);
  },
};

const _stateHandlers = [
  {
    formLoaded: false,
    success: false,
  },
  {
    setFormLoaded: (_, { formLoadedCallback }) => () => {
      if (formLoadedCallback) {
        formLoadedCallback();
      }

      return { formLoaded: true };
    },
    setSuccess: (_, { successCallback }) => () => {
      // if parent passed in callback, call that
      if (successCallback) {
        successCallback();
      }

      return { success: true };
    },
  },
];

const ComposedMarketoForm = compose(
  withStateHandlers(..._stateHandlers),
  lifecycle(_lifecycle),
  branch(
    ({ success }) => !!success,
    renderComponent(({ successMessage }) => (
      <SuccessText>{successMessage}</SuccessText>
    )),
  ),
)(_UnstyledMarketoForm);

// need to add this wrapper so that styled components will work
// styled components require the component to accept the className prop
const UnstyledMarketoForm = ({
  munchkinId,
  formId,
  successCallback,
  successMessage = defaultSuccessMessage,
  className,
  formLoadedCallback,
  hideErrorsUntilSubmit = false,
}) => (
  <div className={className}>
    <ComposedMarketoForm
      munchkinId={munchkinId}
      formId={formId}
      successCallback={successCallback}
      successMessage={successMessage}
      formLoadedCallback={formLoadedCallback}
      hideErrorsUntilSubmit={hideErrorsUntilSubmit}
    />
  </div>
);

export default UnstyledMarketoForm;
